body, a, p, h1, h2, h3 {
  margin: 0;
}

.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
}

.card__img {
  background-image: url("imac.202c52ff.jpeg");
  background-position: center;
  background-size: cover;
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__tittle {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product_code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews {
  color: #616070;
}

.card__prise {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card__prise_text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__prise_number {
  color: #060b35;
  font-size: 16px;
  font-weight: bold;
  line-height: 18px;
}

.card__buy {
  color: #fff;
  cursor: pointer;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  padding-top: 12px;
  padding-bottom: 12px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  transition: background-color .3s, border .3s, color .3s;
  display: flex;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  align-items: center;
  gap: 4px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--0 .stars__star:nth-child(-n), .stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

body {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: normal;
  line-height: 14px;
}

